/* ----------------------------------------------------------------------------
 * Tooltip container
 */
.bsc-tooltip {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

/* ----------------------------------------------------------------------------
 * Tooltip content block (default size 'small')
 */
.bsc-tooltip .bsc-tooltip-content {
  position: absolute;
  z-index: 10001;
  min-width: 160px;
  padding: 8px;
